import {request} from "../axios/request";

const baseUrl = '/api/admin'

const ImageApi = {}

ImageApi.upload = image => {
  return request({
    url: `${baseUrl}/uploadImage`,
    method: 'post',
    data: image
  })
}

ImageApi.loadAll = () => {
  return request({
    url: `${baseUrl}/getAllImages`,
    method: 'get'
  })
}

ImageApi.query = keyword => {
  return request({
    url: `${baseUrl}/queryImages`,
    method: 'get',
    params: {
      keyword
    }
  })
}

ImageApi.delete = id => {
  return request({
    url: `${baseUrl}/deleteImage`,
    method: 'delete',
    params: {
      id
    }
  })
}

export default ImageApi