<template>
  <div class="lwj-container">
    <a-card>
      <a-row class="cards" type="flex" :gutter="16">
        <a-col :span="6" :order="1">
          <div style="float: left; margin-right: 10px;padding-top: 4px">关键词 :</div>
          <a-input style="width: 75%" v-model="keyword" />
        </a-col>
        <a-col :span="4" :order="2">
          <a-button style="margin-right: 10px" type="primary" @click="queryImages">查询</a-button>
          <a-button @click="reset">重置</a-button>
        </a-col>
      </a-row>
      <a-row class="mr-tt">
        <a-upload name="avatar" :showUploadList="false" @change="handleChange">
          <a-button>
            <a-icon type="upload" />点击上传
          </a-button>
          <a-icon class="mr-ll" v-show="loading" type="loading" />
        </a-upload>
      </a-row>
    </a-card>

    <a-row class="mr-t">
      <a-list
        size="small"
        :grid="{ gutter: 16, column: 6 }"
        :pagination="pagination"
        :data-source="images"
      >
        <a-list-item class="imag" slot="renderItem" slot-scope="item">
          <a-card hoverable class="hover" @click="imageInfo(item)" :bodyStyle="{'padding': '10px'}">
            <img style="height: 140px" slot="cover" :src="item.normal_url" :key="item.id" />
            {{item.name}}
          </a-card>
        </a-list-item>
      </a-list>
    </a-row>

    <a-drawer
      title="图片详情"
      placement="right"
      @close="visible = false"
      :maskClosable="true"
      :closable="true"
      :visible="visible"
      :width="width"
    >
      <a-row>
        <a-col>
          <img style="height: 100%; width: 415px" :src="image.normal_url" />
          <a-divider />
          <a-list item-layout="horizontal" style="margin-bottom: 50px">
            <a-list-item>
              <a-list-item-meta :description="image.name">
                <a slot="title">图片名：</a>
              </a-list-item-meta>
            </a-list-item>
            <a-list-item>
              <a-list-item-meta :description="image.size + ' KB'">
                <a slot="title">图片大小：</a>
              </a-list-item-meta>
            </a-list-item>
            <a-list-item>
              <a-list-item-meta :description="image.upload_time">
                <a slot="title">上传日期：</a>
              </a-list-item-meta>
            </a-list-item>
            <a-list-item>
              <a-list-item-meta :description="image.normal_url">
                <span slot="title">
                  普通链接：
                  <a-icon style="color: blue" type="copy" @click="copyText(image.normal_url)" />
                </span>
              </a-list-item-meta>
            </a-list-item>
            <a-list-item>
              <a-list-item-meta :description="image.markdown_text">
                <span slot="title">
                  MarkDown文本：
                  <a-icon style="color: blue" type="copy" @click="copyText(image.markdown_text)" />
                </span>
              </a-list-item-meta>
            </a-list-item>
          </a-list>
        </a-col>
      </a-row>
      <div class="bottom-control">
        <a-button type="danger" @click="deleteImage">删除</a-button>
      </div>
    </a-drawer>
  </div>
</template>

<script>
import ImageApi from "../../api/Images";
export default {
  name: "Images",
  data() {
    return {
      images: [],
      image: {},
      width: "460",
      loading: false,
      visible: false,
      showLoading: true,
      keyword: "",
      pagination: {
        pageSize: 12
      }
    };
  },
  created() {
    this.loadAll();
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.width = window.innerWidth < 680 ? "100%" : "460";
        });
      })();
    };
  },
  methods: {
    async loadAll() {
      ImageApi.loadAll()
        .then(res => {
          if (res.code === 1000) {
            this.images = res.data.images;
          }
        })
        .catch(err => {
          this.$message.error(err.toString());
        });
    },
    handleChange(info) {
      if (info.file.status === "uploading") {
        this.loading = true;
        return;
      }
      this.loading = false;
      let image = new FormData();
      image.append("image", info.file.originFileObj);
      ImageApi.upload(image)
        .then(res => {
          if (res.code === 1000) {
            this.$message.success("上传图片成功");
            this.loadAll();
          } else {
            this.$message.warning("上传图片失败，已存在同名图片");
          }
        })
        .catch(err => {
          this.$message.error(err.toString());
        });
    },
    imageInfo(image) {
      this.visible = true;
      this.image = image;
    },
    async deleteImage() {
      let that = this;
      this.$confirm({
        title: "你确定要删除这张图片吗?",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          ImageApi.delete(that.image.id)
            .then(res => {
              if (res.code === 1000) {
                that.$message.success("删除成功");
                that.visible = false;
                that.loadAll();
              }
            })
            .catch(err => {
              that.$$message.error(err.toString());
            });
        },
        onCancel() {
          console.log("Cancel");
        }
      });
    },
    queryImages() {
      ImageApi.query(this.keyword)
        .then(res => {
          if (res.code === 1000) {
            this.images = res.data.images;
          } else {
            this.$message.warning("查询失败");
          }
        })
        .catch(err => {
          this.$$message.error(err.toString());
        });
    },
    reset() {
      this.keyword = "";
      this.loadAll();
    },
    copyText(text) {
      let oInput = document.createElement("input");
      oInput.value = text;
      document.body.appendChild(oInput);
      oInput.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      document.body.removeChild(oInput);
      this.$message.success("复制成功");
    }
  }
};
</script>

<style lang="less" scoped>
.ant-card-body {
  padding: 5px !important;
}
.bottom-control {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #e8e8e8;
  padding: 10px 16px;
  text-align: right;
  left: 0;
  background: #fff;
  border-radius: 0 0 4px 4px;
}
@media screen and (max-width: 960px) {
  .cards > * {
    width: 100%;
    margin: 10px;
  }
  .mr-tt > * {
    margin-left: 10px;
    margin-top: 0 !important;
  }
  .imag {
    width: 50%;
  }
}
</style>